.dispensary {
  &__info {
    margin-top: 20px;
  }
  &__galery {
    margin-top: 10px;
    display: flex;
    gap: 10px;
    flex-wrap: wrap;
    justify-content: center;
    img {
      object-fit: cover;
      height: 116px;
      flex-basis: calc(50% - 10px);
    }
  }
  &__graphics-items {
    margin-top: 10px;
    display: flex;
    flex-direction: column;
    gap: 20px;
  }
  &__graphics-title {
    margin-top: 40px;
  }
}
